* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

li {
  list-style: none;
  line-height: 25px;
}
button {
  outline: none;
  background: white;
  border: none;
}
.app {
  display: flex;
  flex-direction: column;
  align-items: center;
  color: #e1574b;
}
.break.app {
  color: #4faeae;
}
.title {
  margin: 20px;
  font-size: 40px;
}

.label {
  display: flex;
}
.label > div {
  margin-right: 10px;
  margin-left: 10px;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.length-button {
  display: flex;
}
.label button {
  font-size: 25px;
  border: 0;
}
.label .icon {
  width: 0.7em;
  height: 0.7em;
  margin: 7px;
}
.label p {
  font-size: 22px;
}
#break-label {
  display: flex;
}
#session-label {
  display: flex;
}
.wrapper {
  margin: 30px;
  padding: 6px;
  border-radius: 50%;
  border: 2px solid #f3bcb7;
}

.break .wrapper {
  border: 2px solid #b9dfdf;
}
#time-left {
  height: 300px;
  width: 300px;
  border-radius: 50%;
  border: 20px #f3bcb7 solid;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-size: 55px;
}
.break #time-left {
  border: 20px #b9dfdf solid;
}
.action-button {
  display: flex;
}
.action-button .icon {
  width: 40px;
  height: 40px;
  margin: 5px;
}
.infoTitle,
.sum {
  margin-top: 40px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.infoTitle .icon {
  width: 1.5em;
  height: 1.5em;
}
.sum {
  margin: 5px;
  font-size: 35px;
  font-weight: 1000;
}
.todayTomato {
  margin-left: 5px;
  margin-right: 5px;
  font-size: 16px;
}
.min {
  font-size: 16px;
  font-weight: initial;
  position: relative;
  top: 5px;
}

.icon {
  width: 1em;
  height: 1em;
  vertical-align: -0.15em;
  fill: currentColor;
  overflow: hidden;
}
